import styled from '@emotion/styled'
import { Box, Card, Container, TextareaAutosize, Typography } from '@mui/material'
import { FC, useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import PageHeader from '../PageHeader'
import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import axios, { AxiosResponse } from 'axios'
import { calcSemester } from '../CalcSemeter'
import { useSnackbar } from 'notistack'
import { useAuthUser } from 'react-auth-kit'

interface Questions {
  question1: string
  question2: string
  question3: string
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

const Form: FC<void> = () => {
  const authUser = useAuthUser()
  const params = useParams()
  const clubId = params.id
  const [questions, setQuestions] = useState<Questions>({ question1: '', question2: '', question3: '' })
  const [isEditMode, setIsEditMode] = useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()
  const club = authUser()?.clubs.filter(c => c.id === parseInt(clubId))[0]

  const handleSubmit = async () => {
    axios({
      method: isEditMode ? 'put' : 'post',
      url: `${process.env.REACT_APP_APIHOST}/club/form`,
      data: {
        clubId: parseInt(clubId),
        question1: questions.question1,
        question2: questions.question2,
        question3: questions.question3,
        semester: calcSemester(new Date())
      }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
        } else {
          throw new Error()
        }
      })
      .catch(() => {
        enqueueSnackbar('등록에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_APIHOST}/club/form?clubId=${clubId}`).then((res: AxiosResponse<Questions>) => {
      if (res.data.question1 !== '' || res.data.question2 !== '' || res.data.question3 !== '') {
        setIsEditMode(true)
        setQuestions({ question1: res.data.question1, question2: res.data.question2, question3: res.data.question3 })
      }
    })
  }, [])

  return (
    <>
      <Helmet>
        <title>소개 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club.name} link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Card>
          <Box p={{ xs: 2, md: 5 }}>
            <Typography variant="h2">신청폼 관리</Typography>
            <Typography variant="subtitle1">(커스텀 질문 미사용시 빈칸 처리)</Typography>
            <Box mt={3}>
              <Typography variant="h4">커스텀 질문 1</Typography>
              <CustomTextareaAutosize
                maxRows={4}
                minRows={3}
                value={questions.question1}
                onChange={e =>
                  setQuestions(prevState => {
                    return { ...prevState, question1: e.target.value }
                  })
                }
              />
              <Typography variant="h4">커스텀 질문 2</Typography>
              <CustomTextareaAutosize
                maxRows={4}
                minRows={3}
                value={questions.question2}
                onChange={e =>
                  setQuestions(prevState => {
                    return { ...prevState, question2: e.target.value }
                  })
                }
              />
              <Typography variant="h4">커스텀 질문 3</Typography>
              <CustomTextareaAutosize
                maxRows={4}
                minRows={3}
                value={questions.question3}
                onChange={e =>
                  setQuestions(prevState => {
                    return { ...prevState, question3: e.target.value }
                  })
                }
              />
            </Box>
          </Box>
        </Card>
        <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText={isEditMode ? '수정' : '등록'} />
      </Container>
    </>
  )
}

export default Form
