export const calcSemester = (date: Date) => {
  const year = date.getFullYear()
  const month = date.getMonth()
  let semester = 'SPRING'

  if (month <= 1) {
    semester = 'WINTER'
  } else if (month <= 5) {
    semester = 'SPRING'
  } else if (month <= 7) {
    semester = 'SUMMER'
  } else {
    semester = 'FALL'
  }

  return semester + String(year)
}
